/* eslint-disable */
import { useCallback, useEffect, useState } from 'react';
import { BigNumber } from 'ethers';
import ERC20 from '../polydoge-finance/ERC20';
import usePolyDogeFinance from './usePolyDogeFinance';
import config from '../config';

const useBondsPurchasable = () => {
  const [balance, setBalance] = useState(BigNumber.from(0));
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    async function fetchBondsPurchasable() {
      try {
        setBalance(await polydogeFinance.getBondsPurchasable());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBondsPurchasable();
  }, [setBalance, polydogeFinance]);

  return balance;
};

export default useBondsPurchasable;
