/* eslint-disable */
import React, { useCallback, useMemo } from 'react';
import Page from '../../components/Page';
import { createGlobalStyle } from 'styled-components';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import { useWallet } from 'use-wallet';
import UnlockWallet from '../../components/UnlockWallet';
import PageHeader from '../../components/PageHeader';
import ExchangeCard from './components/ExchangeCard';
import styled from 'styled-components';
import Spacer from '../../components/Spacer';
import useBondStats from '../../hooks/useBondStats';
import usePDDollarStats from '../../hooks/usePDDollarStats';
import usePolyDogeFinance from '../../hooks/usePolyDogeFinance';
import useCashPriceInLastTWAP from '../../hooks/useCashPriceInLastTWAP';
import { useTransactionAdder } from '../../state/transactions/hooks';
import ExchangeStat from './components/ExchangeStat';
import useTokenBalance from '../../hooks/useTokenBalance';
import useBondsPurchasable from '../../hooks/useBondsPurchasable';
import { getDisplayBalance } from '../../utils/formatBalance';
import { BOND_REDEEM_PRICE, BOND_REDEEM_PRICE_BN } from '../../polydoge-finance/constants';
import { Box, Card, CardContent, Button, Typography, Grid } from '@material-ui/core';
import HomeImage from '../../assets/img/background.png';
import ChickImage from '../../assets/img/chick.png';
import LaunchCountdown from '../../components/LaunchCountdown';
import config from '../../config';
const BackgroundImage = createGlobalStyle`
  body {
    background-image: url(${HomeImage});
  }
`;

const Bond: React.FC = () => {
  const { path } = useRouteMatch();
  const { account } = useWallet();
  const polydogeFinance = usePolyDogeFinance();
  const addTransaction = useTransactionAdder();
  const bondStat = useBondStats();
  const pddollarStat = usePDDollarStats();
  const cashPrice = useCashPriceInLastTWAP();

  const bondsPurchasable = useBondsPurchasable();

  const bondBalance = useTokenBalance(polydogeFinance?.PDBOND);
  // const scalingFactor = useMemo(() => (cashPrice ? Number(cashPrice).toFixed(4) : null), [cashPrice]);

  const handleBuyBonds = useCallback(
    async (amount: string) => {
      const tx = await polydogeFinance.buyBonds(amount);
      addTransaction(tx, {
        summary: `Buy ${Number(amount).toFixed(2)} PDBOND with ${amount} PDDOLLAR`,
      });
    },
    [polydogeFinance, addTransaction],
  );

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      const tx = await polydogeFinance.redeemBonds(amount);
      addTransaction(tx, { summary: `Redeem ${amount} PDBOND` });
    },
    [polydogeFinance, addTransaction],
  );
  const isBondRedeemable = useMemo(() => cashPrice.gt(BOND_REDEEM_PRICE_BN), [cashPrice]);
  const isBondPurchasable = useMemo(() => Number(bondStat?.tokenInFtm) < 1.01, [bondStat]);
  const now = Date.now();

  return (
    <Switch>
      <Page>
        <BackgroundImage />
        {/* {now < config.bondLaunchesAt.getTime() ? (
          <LaunchCountdown deadline={config.bondLaunchesAt} description="Home" descriptionLink="/" />
        ) : !!account ? (
          <> */}
            <Box>
              <Grid
                item
                xs={10}
                sm={4}
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  verticalAlign: 'center',
                  overflow: 'hidden',
                  position: 'static',
                  top: '50px',
                  left: '50px',
                }}
              >
                <img src={ChickImage} style={{ maxHeight: '150px', marginLeft: '20%', marginBottom: '5%' }} />
              </Grid>
            </Box>
            <Route exact path={path}>
              <PageHeader icon={'ðŸ¦'} title="Buy &amp; Redeem Bonds" subtitle="Earn premiums upon redemption" />
            </Route>
            <StyledBond>
              <StyledCardWrapper>
                <ExchangeCard
                  action="Purchase"
                  fromToken={polydogeFinance.PDDOLLAR}
                  fromTokenName="PDDOLLAR"
                  toToken={polydogeFinance.PDBOND}
                  toTokenName="PDBOND"
                  priceDesc={
                    !isBondPurchasable
                      ? 'PDDOLLAR is over peg'
                      : getDisplayBalance(bondsPurchasable, 18, 4) + ' PDBOND available for purchase'
                  }
                  onExchange={handleBuyBonds}
                  disabled={!bondStat || isBondRedeemable}
                />
              </StyledCardWrapper>
              <StyledStatsWrapper>
                <ExchangeStat
                  tokenName="1 PDDOLLAR"
                  description="Last-Hour TWAP Price"
                  price={Number(pddollarStat?.tokenInFtm).toFixed(4) || '-'}
                />
                <Spacer size="md" />
                <ExchangeStat
                  tokenName="1 PDBOND"
                  description="Current Price: (PDDOLLAR)"
                  price={Number(bondStat?.tokenInFtm).toFixed(4) || '-'}
                />
              </StyledStatsWrapper>
              <StyledCardWrapper>
                <ExchangeCard
                  action="Redeem"
                  fromToken={polydogeFinance.PDBOND}
                  fromTokenName="PDBOND"
                  toToken={polydogeFinance.PDDOLLAR}
                  toTokenName="PDDOLLAR"
                  priceDesc={`${getDisplayBalance(bondBalance)} PDBOND Available in wallet`}
                  onExchange={handleRedeemBonds}
                  disabled={!bondStat || bondBalance.eq(0) || !isBondRedeemable}
                  disabledDescription={!isBondRedeemable ? `Enabled when 1 PDDOLLAR > ${BOND_REDEEM_PRICE}MATIC` : null}
                />
              </StyledCardWrapper>
            </StyledBond>
          {/* </>
        ) : (
          <UnlockWallet />
        )} */}
      </Page>
    </Switch>
  );
};

const StyledBond = styled.div`
  display: flex;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`;

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`;

const StyledStatsWrapper = styled.div`
  display: flex;
  flex: 0.8;
  margin: 0 20px;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 80%;
    margin: 16px 0;
  }
`;

export default Bond;
