import {useCallback, useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import config from '../config';
import {BigNumber} from 'ethers';

const useCashPriceInLastTWAP = () => {
  const [price, setPrice] = useState<BigNumber>(BigNumber.from(0));
  const polydogeFinance = usePolyDogeFinance();

  const fetchCashPrice = useCallback(async () => {
    setPrice(await polydogeFinance.getPDDollarPriceInLastTWAP());
  }, [polydogeFinance]);

  useEffect(() => {
    fetchCashPrice().catch((err) => console.error(`Failed to fetch PDDOLLAR price: ${err.stack}`));
    const refreshInterval = setInterval(fetchCashPrice, config.refreshInterval);
    return () => clearInterval(refreshInterval);
  }, [setPrice, polydogeFinance, fetchCashPrice]);

  return price;
};

export default useCashPriceInLastTWAP;
