import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {TokenStat} from '../polydoge-finance/types';
import useRefresh from './useRefresh';

const useBondStats = () => {
  const [stat, setStat] = useState<TokenStat>();
  const {slowRefresh} = useRefresh();
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    async function fetchBondPrice() {
      try {
        setStat(await polydogeFinance.getBondStat());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBondPrice();
  }, [setStat, polydogeFinance, slowRefresh]);

  return stat;
};

export default useBondStats;
